/**
 * Servio de autenticao responsvel por gerenciar a sesso do usurio
 * no navegador (armazenamento de token e dados bsicos do usurio).
 *
 * IMPORTANTE: em um ambiente de produo real, a forma mais segura de
 * armazenar tokens de sesso costuma ser via cookies HTTP-only no backend.
 * Como este projeto  100% frontend por enquanto, usamos localStorage
 * apenas para persistncia bsica entre recarregamentos da pgina.
 */

export const AUTH_STORAGE_KEY = 'auth_session_v1'

/**
 * Estrutura bsica dos dados de sesso de autenticao
 */
export interface AuthUser {
  /** Identificador do usurio (pode vir do backend futuramente) */
  id: string
  /** Nome do usurio para exibio na interface */
  name: string
  /** E-mail do usurio autenticado */
  email: string
}

export interface AuthSession {
  /** Token de autenticao (JWT ou similar, quando houver backend) */
  token: string
  /** Dados bsicos do usurio autenticado */
  user: AuthUser
}

/**
 * Salva a sesso de autenticao no localStorage.
 *
 * @param session Sesso de autenticao a ser persistida
 */
export function saveAuthSession(session: AuthSession): void {
  try {
    const serialized = JSON.stringify(session)
    localStorage.setItem(AUTH_STORAGE_KEY, serialized)
  } catch (error) {
    // Em produo, poderamos enviar esse erro para um servio de monitoramento
    console.error('Erro ao salvar sesso de autenticao:', error)
  }
}

/**
 * Recupera a sesso de autenticao do localStorage.
 *
 * @returns AuthSession ou null caso no exista/ocorra erro
 */
export function getAuthSession(): AuthSession | null {
  try {
    const data = localStorage.getItem(AUTH_STORAGE_KEY)
    if (!data) return null

    const parsed = JSON.parse(data) as AuthSession

    // Validao mnima de estrutura para evitar erros em caso de dados corrompidos
    if (!parsed || typeof parsed !== 'object') return null
    if (!parsed.token || !parsed.user) return null

    return parsed
  } catch (error) {
    console.error('Erro ao recuperar sesso de autenticao:', error)
    return null
  }
}

/**
 * Remove a sesso de autenticao do armazenamento local.
 */
export function clearAuthSession(): void {
  try {
    localStorage.removeItem(AUTH_STORAGE_KEY)
  } catch (error) {
    console.error('Erro ao limpar sesso de autenticao:', error)
  }
}

/**
 * Indica rapidamente se existe uma sesso de autenticao vlida
 * armazenada no navegador.
 */
export function hasActiveAuthSession(): boolean {
  return getAuthSession() !== null
}

