import { useState, useEffect, useRef, useCallback } from 'react'

interface WebcamDevice {
  deviceId: string
  label: string
}

type ResolutionMode = 'auto' | 'fullhd' | 'max'

interface UseWebcamReturn {
  videoRef: React.RefObject<HTMLVideoElement>
  canvasRef: React.RefObject<HTMLCanvasElement>
  stream: MediaStream | null
  devices: WebcamDevice[]
  selectedDeviceId: string | null
  isLoading: boolean
  error: string | null
  isStreamActive: boolean
  resolutionMode: ResolutionMode
  setResolutionMode: (mode: ResolutionMode) => Promise<void>
  startCamera: (deviceId?: string) => Promise<void>
  stopCamera: () => void
  capturePhoto: () => string | null
  switchCamera: (deviceId: string) => Promise<void>
  retryCamera: () => Promise<void>
}

// Configuraes de fallback progressivo
const CAMERA_CONSTRAINTS = [
  // Primeira tentativa - qualidade ideal
  {
    video: {
      width: { ideal: 640 },
      height: { ideal: 480 },
      frameRate: { ideal: 30 }
    }
  },
  // Segunda tentativa - qualidade mdia
  {
    video: {
      width: { ideal: 320 },
      height: { ideal: 240 },
      frameRate: { ideal: 15 }
    }
  },
  // Terceira tentativa - sem restries especficas
  {
    video: true
  }
]

// Utilitrio para aguardar um delay
const sleep = (ms: number) => new Promise(resolve => setTimeout(resolve, ms))

// Backoff exponencial com jitter leve para retries de NotReadableError
const backoffDelay = (attempt: number, base = 500, cap = 4000) => {
  const exp = Math.min(cap, base * Math.pow(2, attempt))
  const jitter = Math.floor(Math.random() * 250)
  return exp + jitter
}


export function useWebcam(): UseWebcamReturn {
  const videoRef = useRef<HTMLVideoElement>(null)
  const canvasRef = useRef<HTMLCanvasElement>(null)
  const [stream, setStream] = useState<MediaStream | null>(null)
  const streamRef = useRef<MediaStream | null>(null)
  const [devices, setDevices] = useState<WebcamDevice[]>([])
  const [selectedDeviceId, setSelectedDeviceId] = useState<string | null>(() => {
    try {
      return localStorage.getItem('camera.selectedDeviceId') || null
    } catch {
      return null
    }
  })
  const [isLoading, setIsLoading] = useState(false)
  const [error, setError] = useState<string | null>(null)
  const [isStreamActive, setIsStreamActive] = useState(false)
  // Preferncia de resoluo
  const [resolutionMode, _setResolutionMode] = useState<ResolutionMode>(() => {
    try {
      return (localStorage.getItem('camera.resolutionMode') as ResolutionMode) || 'fullhd'
    } catch {
      return 'fullhd'
    }
  })

  const startSeqRef = useRef(0)
  const selectedDeviceIdRef = useRef<string | null>(selectedDeviceId)
  useEffect(() => {
    selectedDeviceIdRef.current = selectedDeviceId
  }, [selectedDeviceId])


  // Log de debug (silenciado)
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const debugLog = useCallback((..._args: unknown[]) => {
    // silenciado conforme solicitado
  }, [])


  // Registro de tentativas para feedback ao usurio
  const attemptSummaryRef = useRef<string[]>([])
  const resetAttemptLog = useCallback(() => {
    attemptSummaryRef.current = []
  }, [])

  const logAttempt = useCallback((msg: string) => {
    attemptSummaryRef.current.push(msg)
    debugLog()
  }, [debugLog])

  // Listar dispositivos de cmera disponveis
  const getDevices = useCallback(async () => {
    try {
      debugLog('Listando dispositivos disponveis...')
      const mediaDevices = await navigator.mediaDevices.enumerateDevices()
      const videoDevices = mediaDevices
        .filter(device => device.kind === 'videoinput')
        .map((device, index) => ({
          deviceId: device.deviceId,
          label: device.label || `Cmera ${index + 1}`
        }))

      debugLog('Dispositivos encontrados:', videoDevices)
      setDevices(videoDevices)
      return videoDevices
    } catch (err) {
      debugLog('Erro ao listar dispositivos:', err)
      console.error('Erro ao listar dispositivos:', err)
      setError('Erro ao listar cmeras disponveis')
      return []
    }
  }, [debugLog])

  // Parar stream e limpar recursos
  const cleanupStream = useCallback(() => {
    debugLog('Limpando recursos do stream...')
    const current = streamRef.current

    if (current) {
      current.getTracks().forEach(track => {
        track.stop()
        debugLog(`Track parado: ${track.kind}`)
      })
    }
    streamRef.current = null
    setStream(null)

    if (videoRef.current) {
      videoRef.current.srcObject = null
    }

    setIsStreamActive(false)
  }, [debugLog])

  // Atualiza a resoluo no track atual (se existir) e salva preferncia
  const setResolutionMode = useCallback(async (mode: ResolutionMode) => {
    debugLog('Alterando modo de resoluo...', { mode })
    _setResolutionMode(mode)
    try {
      localStorage.setItem('camera.resolutionMode', mode)
    } catch {
      // Falha ao salvar preferncia no localStorage
    }


    const current = streamRef.current
    const track = current?.getVideoTracks()[0]
    if (!track) return

    try {
      if (mode === 'fullhd') {
        await track.applyConstraints({ width: { ideal: 1920 }, height: { ideal: 1080 } })
      } else if (mode === 'max') {
        interface VideoCapabilities {
          width?: { max?: number }
          height?: { max?: number }
        }

        const trackWithCapabilities = track as MediaStreamTrack & {
          getCapabilities?: () => VideoCapabilities
        }

        const caps = trackWithCapabilities.getCapabilities ? trackWithCapabilities.getCapabilities() : undefined
        if (caps?.width?.max && caps?.height?.max) {
          await track.applyConstraints({ width: caps.width.max, height: caps.height.max })
        }
      } else {
        // volta para automtico (sem forar dimenses especficas)
        await track.applyConstraints({})
      }
      debugLog('Resoluo aplicada com sucesso ao stream atual')
    } catch (e) {
      debugLog('Falha ao aplicar resoluo ao stream atual', e)
    }
  }, [debugLog])


  // Tentar com diferentes cmeras se a atual falhar
  const tryWithDifferentCameras = useCallback(async (excludeDeviceId?: string): Promise<MediaStream> => {
    const availableDevices = devices.filter(device => device.deviceId !== excludeDeviceId)

    for (const device of availableDevices) {
      try {
        logAttempt(`Tentando cmera alternativa: ${device.label}`)
        const constraints = { video: { deviceId: { ideal: device.deviceId } } }
        const mediaStream = await navigator.mediaDevices.getUserMedia(constraints)
        logAttempt(` Sucesso com cmera alternativa: ${device.label}`)
        return mediaStream
      } catch (err) {
        const error = err as Error & { name?: string }
        logAttempt(`Falha com cmera ${device.label}: ${error?.name || 'Erro desconhecido'}`)
        debugLog(`Falha com cmera ${device.label}:`, err)
        continue
      }
    }

    throw new Error('Todas as cmeras falharam')
  }, [devices, debugLog, logAttempt])

  // Tentar todas as cmeras disponveis
  const tryAllCameras = useCallback(async (): Promise<MediaStream> => {
    resetAttemptLog()
    logAttempt(`Buscando cmera disponvel (${devices.length} detectada${devices.length !== 1 ? 's' : ''})...`)

    debugLog(`Tentando todas as ${devices.length} cmeras disponveis...`)

    for (let i = 0; i < devices.length; i++) {
      const device = devices[i]
      logAttempt(`Testando ${device.label}`)

      debugLog(`Testando cmera ${i + 1}/${devices.length}: ${device.label}`)

      // Tentar com constraints progressivos para esta cmera
      for (let constraintIndex = 0; constraintIndex < CAMERA_CONSTRAINTS.length; constraintIndex++) {
        const baseConstraints = CAMERA_CONSTRAINTS[constraintIndex]
        const constraints = {
          ...baseConstraints,
          video: typeof baseConstraints.video === 'object' ? {
            ...baseConstraints.video,
            deviceId: { ideal: device.deviceId }
          } : { deviceId: { ideal: device.deviceId } }
        }

        try {
          debugLog(`  Tentando constraint ${constraintIndex + 1} para ${device.label}:`, constraints)
          const mediaStream = await navigator.mediaDevices.getUserMedia(constraints)
          debugLog(` Sucesso com ${device.label}!`)
          logAttempt(` Sucesso com ${device.label}`)
          return mediaStream
        } catch (err) {
          const error = err as Error & { name?: string }
          debugLog(`  Falha constraint ${constraintIndex + 1} para ${device.label}:`, err)
          logAttempt(`Falha (${error?.name || 'Erro'}) em ${device.label} [tentativa ${constraintIndex + 1}]`)

          // Para NotReadableError, aguardar antes da prxima tentativa com backoff exponencial + jitter
          if (err instanceof Error && err.name === 'NotReadableError') {
            const delay = backoffDelay(constraintIndex)
            debugLog(`  Aguardando ${Math.round(delay/100)/10}s antes da prxima tentativa...`)
            await sleep(delay)
          }
        }
      }
    }

    throw new Error(`Todas as ${devices.length} cmeras falharam com todos os constraints`)
  }, [devices, debugLog, logAttempt, resetAttemptLog])

  // Tentar inicializar stream com configuraes progressivas
  const tryGetUserMedia = useCallback(async (deviceId?: string, retryCount = 0): Promise<MediaStream> => {
    // Se no especificou deviceId
    if (!deviceId) {
      if (devices.length > 0) {
        // J temos lista de dispositivos - tentar todas as cmeras
        logAttempt('Nenhuma cmera especfica selecionada; tentando todas as cmeras disponveis...')
        debugLog('Nenhuma cmera especfica, tentando todas...')
        return tryAllCameras()
      } else {
        // Dispositivos ainda no enumerados - solicitar acesso genrico para destravar permisses
        const baseConstraints = CAMERA_CONSTRAINTS[Math.min(retryCount, CAMERA_CONSTRAINTS.length - 1)]
        const genericConstraints: MediaStreamConstraints =
          typeof baseConstraints.video === 'object'
            ? { video: { ...(baseConstraints.video as Record<string, unknown>) } }
            : { video: true }

        debugLog(`Tentativa ${retryCount + 1}/${CAMERA_CONSTRAINTS.length} com constraints genricos:`, genericConstraints)
        try {
          const mediaStream = await navigator.mediaDevices.getUserMedia(genericConstraints)
          debugLog(`Sucesso na tentativa ${retryCount + 1}`)
          return mediaStream
        } catch (err) {
          debugLog(`Falha na tentativa ${retryCount + 1}:`, err)
          if (err instanceof Error && err.name === 'NotReadableError') {
            const delay = backoffDelay(retryCount)
            debugLog(`NotReadableError detectado, aguardando ${Math.round(delay/100)/10} segundos...`)
            await sleep(delay)
            // Aps aguardar, tentar enumerar dispositivos e buscar alternativas
            const freshDevices = await getDevices()
            if (freshDevices.length > 0) {
              logAttempt('Cmera padro ocupada. Tentando alternativas automaticamente...')
              try {
                return await tryAllCameras()
              } catch {
                // Se todas alternativas tambm falharem, continua o fluxo de retry abaixo
              }
            }
          }
          if (retryCount + 1 < CAMERA_CONSTRAINTS.length) {
            return tryGetUserMedia(undefined, retryCount + 1)
          }
          throw err as Error
        }
      }
    }

    const maxRetries = CAMERA_CONSTRAINTS.length

    if (retryCount >= maxRetries) {
      // Se chegou ao limite com a cmera especfica, tentar outras cmeras
      if (devices.length > 1 && deviceId) {
        debugLog('Limite atingido, tentando cmeras alternativas...')
        return tryWithDifferentCameras(deviceId)
      }
      throw new Error('Todas as tentativas de acesso  cmera falharam')
    }

    const baseConstraints = CAMERA_CONSTRAINTS[retryCount]
    const videoConstraintsBase = (typeof baseConstraints.video === 'object') ? { ...baseConstraints.video } : {}

    // Se o modo for FULL HD, fora ideal 1920x1080 ao solicitar o stream
    if (resolutionMode === 'fullhd') {
      (videoConstraintsBase as Record<string, unknown>).width = { ideal: 1920 }
      ;(videoConstraintsBase as Record<string, unknown>).height = { ideal: 1080 }
    }

    const constraints: MediaStreamConstraints = {
      video: {
        ...(videoConstraintsBase as Record<string, unknown>),
        deviceId: { ideal: deviceId }
      }
    }

    debugLog(`Tentativa ${retryCount + 1}/${maxRetries} com constraints:`, constraints)

    try {
      const mediaStream = await navigator.mediaDevices.getUserMedia(constraints)
      debugLog(`Sucesso na tentativa ${retryCount + 1}`)
      return mediaStream
    } catch (err) {
      debugLog(`Falha na tentativa ${retryCount + 1}:`, err)

      // Para NotReadableError, aumentar o delay progressivamente e tentar alternativas
      if (err instanceof Error && err.name === 'NotReadableError') {
        const delay = backoffDelay(retryCount) // backoff com jitter
        debugLog(`NotReadableError detectado, aguardando ${Math.round(delay/100)/10} segundos...`)
        await sleep(delay)

        // Antes de insistir na mesma cmera, tente outras automaticamente
        if (devices.length > 1 && deviceId) {
          try {
            const failingLabel = devices.find(d => d.deviceId === deviceId)?.label || 'cmera selecionada'
            logAttempt(`Cmera ocupada (${failingLabel}). Tentando alternativas...`)
            const altStream = await tryWithDifferentCameras(deviceId)
            return altStream
          } catch {
            // Se alternativas tambm falharem, continua o retry normal
          }
        }
      }

      return tryGetUserMedia(deviceId, retryCount + 1)
    }
  }, [debugLog, devices, tryWithDifferentCameras, tryAllCameras, resolutionMode, getDevices, logAttempt])

  // Iniciar cmera com retry inteligente ( prova de chamadas simultneas/StrictMode)
  const startCamera = useCallback(async (deviceId?: string) => {
    debugLog('Iniciando cmera...', { deviceId })
    const mySeq = ++startSeqRef.current
    setIsLoading(true)
    setError(null)
    resetAttemptLog()

    // Respeita preferncia persistida quando nenhum deviceId  passado
    const desiredDeviceId = deviceId ?? selectedDeviceIdRef.current ?? undefined

    // Se j existe um stream ativo com o mesmo device, evita reiniciar
    const currentTrackDeviceId = streamRef.current?.getVideoTracks()[0]?.getSettings().deviceId
    if (streamRef.current && (!desiredDeviceId || desiredDeviceId === currentTrackDeviceId)) {
      debugLog('Stream j ativo com o mesmo dispositivo, ignorando start.')
      setIsLoading(false)
      return
    }

    try {
      // Limpar recursos anteriores
      cleanupStream()

      // Pequeno intervalo para garantir liberao de recursos do SO
      await sleep(200)

      // Tentar obter stream com fallback progressivo
      const mediaStream = await tryGetUserMedia(desiredDeviceId)

      // Se durante a espera outra inicializao comeou, descarta este stream
      if (startSeqRef.current !== mySeq) {
        mediaStream.getTracks().forEach(t => t.stop())
        debugLog('Inicializao obsoleta descartada (antes de anexar ao vdeo)')
        return
      }

      // Aplicar preferncia de resoluo ao track antes de vincular ao vdeo
      try {
        const track = mediaStream.getVideoTracks()[0]
        if (resolutionMode === 'fullhd') {
          await track.applyConstraints({ width: { ideal: 1920 }, height: { ideal: 1080 } })
        } else if (resolutionMode === 'max') {
          interface VideoCapabilities {
            width?: { max?: number }
            height?: { max?: number }
          }

          const trackWithCapabilities = track as MediaStreamTrack & {
            getCapabilities?: () => VideoCapabilities
          }

          const caps = trackWithCapabilities.getCapabilities ? trackWithCapabilities.getCapabilities() : undefined
          if (caps?.width?.max && caps?.height?.max) {
            await track.applyConstraints({ width: caps.width.max, height: caps.height.max })
          }
        }
      } catch (e) {
        debugLog('Falha ao aplicar resoluo na inicializao', e)
      }

      // Conectar stream ao elemento de vdeo
      if (videoRef.current) {
        const video = videoRef.current
        try {
          video.pause()
        } catch {
          // Falha ao pausar vdeo (pode no estar reproduzindo)
        }
        video.srcObject = null
        video.srcObject = mediaStream

        // Aguardar o vdeo estar pronto
        await new Promise<void>((resolve, reject) => {
          const onLoadedMetadata = () => {
            debugLog('Vdeo carregado com sucesso', {
              width: video.videoWidth,
              height: video.videoHeight
            })
            video.removeEventListener('loadedmetadata', onLoadedMetadata)
            video.removeEventListener('error', onError)
            resolve()
          }

          const onError = (e: Event) => {
            debugLog('Erro ao carregar vdeo:', e)
            video.removeEventListener('loadedmetadata', onLoadedMetadata)
            video.removeEventListener('error', onError)
            reject(new Error('Erro ao carregar stream de vdeo'))
          }

          video.addEventListener('loadedmetadata', onLoadedMetadata)
          video.addEventListener('error', onError)

          video.play().then(resolve).catch(playErr => {
            debugLog('Erro ao reproduzir vdeo:', playErr)
            // Se uma nova carga foi iniciada (StrictMode/duas chamadas), ignore AbortError
            const error = playErr as Error & { name?: string; code?: number }
            const name = error?.name
            if (name === 'AbortError' || error?.code === 20) {
              if (startSeqRef.current !== mySeq) {
                resolve()
                return
              }
            }
            reject(playErr)
          })
        })
      }

      // Se durante o carregamento outra inicializao comeou, descarta este stream
      if (startSeqRef.current !== mySeq) {
        mediaStream.getTracks().forEach(t => t.stop())
        debugLog('Inicializao obsoleta descartada (aps play)')
        return
      }

      // Configurar estado
      streamRef.current = mediaStream
      setStream(mediaStream)
      const actualDeviceId = mediaStream.getVideoTracks()[0]?.getSettings().deviceId
      setSelectedDeviceId(desiredDeviceId || actualDeviceId || null)

      // Persistir dispositivo selecionado
      try {
        localStorage.setItem('camera.selectedDeviceId', (actualDeviceId || desiredDeviceId || '') as string)
      } catch {
        // Falha ao salvar dispositivo no localStorage
      }

      setIsStreamActive(true)

      debugLog('Cmera iniciada com sucesso!', {
        selectedDevice: actualDeviceId,
        tracks: mediaStream.getTracks().length
      })

      // Atualizar lista de dispositivos aps obter permisses
      await getDevices()

    } catch (err) {
      const error = err as Error & { name?: string; message?: string }
      debugLog('Erro final ao iniciar cmera:', err)
      console.error('Erro ao acessar webcam:', err)

      // Se a chamada atual for obsoleta, no apresente erro ao usurio
      if (error?.name === 'AbortError' && startSeqRef.current !== mySeq) {
        return
      }

      let errorMessage = 'Erro ao acessar a webcam'
      const attempts = attemptSummaryRef.current
      const attemptsText = attempts.length ? `\n\nTentativas realizadas:\n ${attempts.slice(0, 8).join('\n ')}` : ''

      if (error instanceof Error) {
        switch (error.name) {
          case 'NotAllowedError':
            errorMessage = 'Acesso  webcam negado. Clique no cone da cmera na barra de endereos e permita o acesso.'
            break
          case 'NotFoundError':
            errorMessage = 'Nenhuma webcam encontrada. Verifique se h uma cmera conectada.'
            break
          case 'NotReadableError': {
            const failingLabel = desiredDeviceId ? (devices.find(d => d.deviceId === desiredDeviceId)?.label || null) : null
            errorMessage = `No foi possvel acessar a cmera${failingLabel ? ` (${failingLabel})` : ''} porque ela parece estar ocupada por outro aplicativo.

Aes automticas realizadas:
 ${attempts.length ? attempts.join('\n ') : 'Tentamos reestabelecer o acesso com diferentes configuraes e dispositivos'}

O que voc pode fazer agora:
 ${devices.length > 1 ? 'Troque para outra cmera nas configuraes (boto "Trocar cmera")' : 'Feche aplicativos que possam estar usando a cmera (Teams, Zoom, Skype, etc.)'}
 Clique em "Tentar novamente"
 Verifique as permisses do navegador (cone da cmera na barra de endereos)`
            break
          }
          case 'OverconstrainedError':
            errorMessage = 'Configuraes da cmera no suportadas. Tente uma cmera diferente.'
            break
          default:
            if (error.message?.includes('Todas as tentativas') || error.message?.includes('Todas as cmeras')) {
              errorMessage = `Todas as cmeras falharam.

 Verificaes:
 Certifique-se que nenhum outro app est usando a webcam
 Feche completamente outros navegadores
 Reinicie o computador se necessrio
 Verifique se a webcam funciona em outros aplicativos

${devices.length} cmera${devices.length > 1 ? 's' : ''} detectada${devices.length > 1 ? 's' : ''}${attemptsText}`
            } else {
              errorMessage = `Erro ao acessar webcam: ${error.message}${attemptsText}`
            }
        }
      }

      setError(errorMessage)
      setIsStreamActive(false)
      cleanupStream()
    } finally {
      setIsLoading(false)
    }
  }, [cleanupStream, tryGetUserMedia, getDevices, debugLog, devices, resolutionMode, resetAttemptLog])

  // Parar cmera
  const stopCamera = useCallback(() => {
    debugLog('Parando cmera...')
    cleanupStream()
  }, [debugLog, cleanupStream])

  // Capturar foto
  const capturePhoto = useCallback((): string | null => {
    debugLog('Tentando capturar foto...', {
      hasVideo: !!videoRef.current,
      hasCanvas: !!canvasRef.current,
      isStreamActive
    })

    if (!videoRef.current || !canvasRef.current || !isStreamActive) {
      debugLog('Captura cancelada - requisitos no atendidos')
      return null
    }

    const video = videoRef.current
    const canvas = canvasRef.current
    const context = canvas.getContext('2d')

    if (!context) {
      debugLog('Erro: contexto 2d do canvas no disponvel')
      return null
    }

    // Verificar se o vdeo tem dimenses vlidas
    if (video.videoWidth === 0 || video.videoHeight === 0) {
      debugLog('Erro: vdeo sem dimenses vlidas', {
        width: video.videoWidth,
        height: video.videoHeight
      })
      return null
    }

    // Definir dimenses do canvas baseado no vdeo
    canvas.width = video.videoWidth
    canvas.height = video.videoHeight

    debugLog('Capturando frame...', {
      videoWidth: video.videoWidth,
      videoHeight: video.videoHeight
    })

    // Desenhar frame atual do vdeo no canvas
    context.drawImage(video, 0, 0, canvas.width, canvas.height)

    // Converter para data URL (base64)
    const dataUrl = canvas.toDataURL('image/jpeg', 0.9)
    debugLog('Foto capturada com sucesso!', {
      size: Math.round(dataUrl.length / 1024) + 'KB'
    })

    return dataUrl
  }, [isStreamActive, debugLog])

  // Retry camera - til para tentar novamente aps erro
  const retryCamera = useCallback(async () => {
    debugLog('Tentando novamente...')
    await startCamera(selectedDeviceId || undefined)
  }, [startCamera, selectedDeviceId, debugLog])

  // Trocar cmera
  const switchCamera = useCallback(async (deviceId: string) => {
    debugLog('Trocando cmera...', { newDeviceId: deviceId })
    await startCamera(deviceId)
  }, [startCamera, debugLog])

  // Limpar recursos ao desmontar componente
  useEffect(() => {
    return () => {
      debugLog('Componente desmontando, limpando recursos...')
      cleanupStream()
    }
  }, [debugLog, cleanupStream])

  // Carregar dispositivos na inicializao (sem iniciar cmera automaticamente)
  useEffect(() => {
    debugLog('Hook inicializado, carregando dispositivos...')
    getDevices()
  }, [getDevices, debugLog])

  return {
    videoRef,
    canvasRef,
    stream,
    devices,
    selectedDeviceId,
    isLoading,
    error,
    isStreamActive,
    resolutionMode,
    setResolutionMode,
    startCamera,
    stopCamera,
    capturePhoto,
    switchCamera,
    retryCamera
  }
}