import { createContext, useContext, useEffect, useMemo, useState } from 'react'
import { useMutation } from '@tanstack/react-query'
import {
  type AuthSession,
  type AuthUser,
  clearAuthSession,
  getAuthSession,
  saveAuthSession,
} from '@/services/authService'

/**
 * Tipagem dos dados necessrios para realizar o login.
 * Nesse momento n�e3o h�e1 integra�e7�e3o com backend, mas
 * a interface j�e1 est�e1 pronta para ser reaproveitada.
 */
export interface LoginCredentials {
  /** Campo que aceita tanto e-mail quanto nome de usu�e1rio */
  emailOrUsername: string
  /** Senha digitada pelo usu�e1rio */
  password: string
}

/**
 * Interface exposta pelo contexto de autentica�e7�e3o.
 */
export interface AuthContextValue {
  user: AuthUser | null
  token: string | null
  isAuthenticated: boolean
  /** true enquanto carregamos sess�e3o inicial ou durante login */
  isLoading: boolean
  /** Mensagem de erro de login (se houver) */
  loginError: string | null
  /**
   * Realiza o fluxo de login.
   * Em breve este m�e9todo deve chamar a API real de autentica�e7�e3o.
   */
  login: (credentials: LoginCredentials) => Promise<void>
  /** Limpa sess�e3o local e desautentica o usu�e1rio */
  logout: () => void
}

const AuthContext = createContext<AuthContextValue | undefined>(undefined)

interface AuthProviderProps {
  children: React.ReactNode
}

/**
 * Provider respons�e1vel por centralizar o estado de autentica�e7�e3o
 * da aplica�e7�e3o.
 */
export function AuthProvider({ children }: AuthProviderProps) {
  const [user, setUser] = useState<AuthUser | null>(null)
  const [token, setToken] = useState<string | null>(null)
  const [isInitializing, setIsInitializing] = useState(true)
  const [loginError, setLoginError] = useState<string | null>(null)

  // Carrega sess�e3o previamente salva (se existir)
  useEffect(() => {
    const session = getAuthSession()
    if (session) {
      setUser(session.user)
      setToken(session.token)
    }
    setIsInitializing(false)
  }, [])

  /**
   * Muta�e7�e3o de login usando React Query.
   * Por enquanto, o "login" apenas aceita qualquer combina�e7�e3o de
   * usu�e1rio/senha, gera um token e persiste a sess�e3o localmente.
   *
   * Quando o backend estiver dispon�evel, basta trocar a fun�e7�e3o
   * `mutationFn` para chamar a API real.
   */
  const loginMutation = useMutation<{ token: string; user: AuthUser }, Error, LoginCredentials>({
    mutationKey: ['auth', 'login'],
    mutationFn: async ({ emailOrUsername }: LoginCredentials) => {
      // Simula um pequeno delay de rede para feedback visual
      await new Promise((resolve) => setTimeout(resolve, 500))

      // Gera um token simples baseado nos dados de entrada
      const token = btoa(`${emailOrUsername}:${Date.now()}`)

      const user: AuthUser = {
        id: emailOrUsername.toLowerCase(),
        name: emailOrUsername,
        email: emailOrUsername,
      }

      const session: AuthSession = { token, user }
      saveAuthSession(session)

      return session
    },
    onSuccess: (session) => {
      setUser(session.user)
      setToken(session.token)
      setLoginError(null)
    },
    onError: (error) => {
      console.error('Erro no fluxo de login:', error)
      setLoginError('N�e3o foi poss�edvel realizar o login. Tente novamente.')
    },
  })

  const login = async (credentials: LoginCredentials) => {
    setLoginError(null)
    await loginMutation.mutateAsync(credentials)
  }

  const logout = () => {
    clearAuthSession()
    setUser(null)
    setToken(null)
    setLoginError(null)
  }

  const value: AuthContextValue = useMemo(() => {
    return {
      user,
      token,
      isAuthenticated: Boolean(user && token),
      isLoading: isInitializing || loginMutation.isPending,
      loginError,
      login,
      logout,
    }
  }, [user, token, isInitializing, loginMutation.isPending, loginError])

  return <AuthContext.Provider value={value}>{children}</AuthContext.Provider>
}

/**
 * Hook de conveni�eancia para acesso ao contexto de autentica�e7�e3o.
 *
 * Lembrar de sempre usar este hook apenas dentro de componentes
 * que est�e3o abaixo de <AuthProvider> na �e1rvore de componentes.
 */
export function useAuth(): AuthContextValue {
  const context = useContext(AuthContext)
  if (!context) {
    throw new Error('useAuth deve ser usado dentro de um AuthProvider')
  }
  return context
}

