import { useState, useEffect } from 'react'
import { getAllRecords } from '@/services/storageService'
import type { InspectionRecord } from '@/types/inspection'

/**
 * Interface para as estatsticas de inspeo
 */
export interface InspectionStats {
  /** Total geral de inspees realizadas */
  total: number
  /** Total de inspees aprovadas */
  aprovados: number
  /** Total de inspees reprovadas */
  reprovados: number
}

/**
 * Hook customizado para calcular estatsticas de inspeo do LocalStorage
 * 
 * Calcula automaticamente:
 * - Total de inspees realizadas
 * - Total de inspees aprovadas (statusFinal === 'APROVADO')
 * - Total de inspees reprovadas (statusFinal === 'REPROVADO')
 * 
 * @returns Objeto com as estatsticas calculadas
 * 
 * @example
 * ```tsx
 * function MyComponent() {
 *   const stats = useInspectionStats()
 *   
 *   return (
 *     <div>
 *       <p>Total: {stats.total}</p>
 *       <p>Aprovados: {stats.aprovados}</p>
 *       <p>Reprovados: {stats.reprovados}</p>
 *     </div>
 *   )
 * }
 * ```
 */
export function useInspectionStats(): InspectionStats {
  const [stats, setStats] = useState<InspectionStats>({
    total: 0,
    aprovados: 0,
    reprovados: 0
  })

  useEffect(() => {
    // Funo para calcular as estatsticas
    const calculateStats = () => {
      const records: InspectionRecord[] = getAllRecords()

      const total = records.length
      const aprovados = records.filter(record => record.statusFinal === 'APROVADO').length
      const reprovados = records.filter(record => record.statusFinal === 'REPROVADO').length

      setStats({
        total,
        aprovados,
        reprovados
      })
    }

    // Calcula as estatsticas inicialmente
    calculateStats()

    // Listener para detectar mudanas no localStorage de outras abas/janelas
    const handleStorageChange = (e: StorageEvent) => {
      // Recalcula apenas se a chave 'inspection_records' foi modificada
      if (e.key === 'inspection_records' || e.key === null) {
        calculateStats()
      }
    }

    // Listener para detectar mudanas no localStorage da mesma aba (evento customizado)
    const handleCustomUpdate = () => {
      calculateStats()
    }

    // Adiciona listeners
    window.addEventListener('storage', handleStorageChange)
    window.addEventListener('inspectionRecordsUpdated', handleCustomUpdate)

    // Cleanup: remove listeners quando o componente for desmontado
    return () => {
      window.removeEventListener('storage', handleStorageChange)
      window.removeEventListener('inspectionRecordsUpdated', handleCustomUpdate)
    }
  }, [])

  return stats
}

